/* General styles */
body {
    margin: 0;
    font-family: 'Roboto', sans-serif;
  }
  
  /* Header container */
  .header-container {
    height: 70px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
    background-color: #1a1a1a;
   box-shadow: 20px;
    color: white;
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
    z-index: 1000;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    transition: background-color 0.3s ease;
  }
  
  .header-container.scrolled {
    background-color: #0d0d0d;
  }
  
  /* Brand styling */
  .brand {
    font-size: 1.8rem;
    font-weight: bold;
    color: #ffcc00;
    text-transform: uppercase;
    letter-spacing: 2px;
  }
  
  /* Menu Icon (hamburger) */
  .menu-icon {
    display: none;
    font-size: 1.5rem;
    cursor: pointer;
    color: #ffcc00;
    
  }
  
  /* Menu Links for Large Screens */
  .menu-links {
    display: none; /* Hide on small screens */
  }
  
  .menu-link {
    margin-left: 20px;
    color: white;
    text-decoration: none;
    font-size: 1.3rem;
    transition: color 0.3s ease;
    cursor: pointer;
    overflow-y: auto;
    
  }
  
  .menu-link:hover {
    color: #ffcc00;
  }
  
  /* Mobile Menu */
  .mobile {
    background-color: #333;
    position: fixed;
    top: 0;
    right: 0;
    width: 250px;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 20px;
    transition: transform 0.3s ease-in-out;
    transform: translateX(100%); /* Initially hidden */
    z-index: 999;
    box-shadow: -2px 0 5px rgba(0, 0, 0, 0.5);
  }
  
  .mobile.show {
    transform: translateX(0); /* Slide in when visible */
  }
  
  .mobile .close-icon {
    align-self: flex-end;
    font-size: 1.5rem;
    cursor: pointer;
    color: #ffcc00;
  }
  
  .mobile nav {
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    width: 100%;
  }
  
  
  .mobile nav a {
    margin-bottom: 15px;
    color: white;
    text-decoration: none;
    font-size: 1.2rem;
    transition: color 0.3s ease;
  }
  
  .mobile nav a:hover {
    color: #ffcc00;
  }
  
  /* Download CV button */
  .download-cv {
    
    justify-content: center;
    padding: 10px 15px;
    background-color: #ffcc00;
    color: white;
    text-decoration: none;
    border-radius: 5px;
    transition: background-color 0.3s ease;
  }
  
  .download-cv:hover {
    background-color: #e6b800;
  }
  
  /* Responsive Design */
  
  /* On small screens, show the hamburger menu and mobile menu */
  @media screen and (max-width: 768px) {
    .menu-icon {
      display: block;
      padding: 0 15px;
    }
    .download-cv {
      display: none;
    }
    .header-container .menu-links {
      display: none; /* Hide menu links on small screens */
    }
  }
  
  /* On large screens, show the menu links in a horizontal row */
  @media screen and (min-width: 769px) {
    .header-container {
      height: 80px;
      padding: 0 40px;
    }
    .menu-links {
      display: flex; /* Show menu links on large screens */
      flex-direction: row;
    }
    .mobile {
      display: none; /* Hide the mobile menu on large screens */
    }
    .download-cv {
      display: block;
    }
  }
  @media screen and (min-width: 770px) and (max-width: 1001px) {
    .download-cv {
        font-size: 10px;
        text-align: center;
        height: auto;
        row-gap: 5px;
        display: flex;
        margin-top: 10px;
        margin-right: 2px;
      
    }
    .menu-link{
        font-size: 1rem;
        padding-right: 10px;
        row-gap: 3px;
    }
    .brand{
        font-size: 1.2rem;
        left: 2px;
        text-align: center;
    }
  }
  